.nav-link {
  font-size: 1.3rem;

  text-align: center;
  &:hover {
    color: gold !important;
  }
}

.navbar-brand:hover > a > svg {
  color: gold;
}

.navbar {
  z-index: 2;
  position: fixed !important;
  top: 0rem;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.7);
}
.btn-logOut {
  cursor: pointer;
}
.btn-logOut:hover {
  color: red !important;
}
.hover-gold:hover,
svg:hover {
  cursor: pointer;
}
.link {
  text-decoration: none!important;
  &:hover{
    color:gold!important;
  }
}

@media (min-width: 1048px) {
  .nav-cart {
    display: none;
  }
}

@media (max-width: 1048px) and (max-height: 2000px) {
  .nav-link {
    width: 100vw;
  }
  .nav-cart-lg {
    display: none !important;
  }
}
