
.loading {
    position: absolute!important;
    top: -7%!important;
    left: 7% !important;text-transform: capitalize;
    text-align: center;
    margin-top: 3rem;
  }
  .login {
    height: 21rem;
    width: 30vw;
    background-color: rgba(0, 0, 0, 0.8);
    border-radius: 12px;
    position: fixed;
    z-index: 2;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    padding: 2rem;
    border: solid 3px gray;
    animation: EErow ease-in 0.3s;
  }
  .login-closeBtn svg {
    position: absolute;
    top: 0%;
    right: 0%;
  }
  @media (max-width: 1048px) and ( max-height:2000px) {

   
    .login {
      height: 80vh!important;
      width: 100vw!important;
      overflow: scroll;
    }
  
  }
  @media (max-height: 500px) {
    .login {
      height: 80vh;
      width: 50vw;
    }
  
  }
  

  